<template>
  <div style="height: 100vh" class="d-flex">
    <v-row align="center" justify="center">
      <v-col cols="12" sm="8" md="4">
        <v-card class="elevation-12 below-toolbar">
          <v-card-title>
            <span class="headline">Login</span>
          </v-card-title>
          <v-card-text>
            <v-form ref="loginForm" lazy-validation @submit.prevent="handleSubmit">
              <v-text-field
                label="Username"
                required
                autofocus
                v-model="name"
                v-on:keydown.enter.prevent="handleSubmit"
              />
              <v-text-field
                label="Password"
                required
                type="password"
                v-model="password"
                v-on:keydown.enter.prevent="handleSubmit"
              />
            </v-form>
            <span v-show="!this.name || !this.password">enter your username and password</span>
            {{ msg }}
          </v-card-text>
          <v-card-actions>
            <v-spacer />
            <v-btn small text @click="clear">Clear</v-btn>
            <v-btn
              x-small
              class="mr-1 btn-height-22 white--text"
              color="primary"
              v-on:click="handleSubmit"
              v-bind:disabled="!this.name || !this.password"
              >Login</v-btn
            >
          </v-card-actions>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import axios from '../axios';
import * as localstorageUtil from '../utils/localstorage';

export default {
  data() {
    return {
      name: '',
      password: '',
      msg: '',
    };
  },
  computed: {
    setting() {
      return this.$store.state.setting;
    },
  },
  mounted() {},
  methods: {
    async handleSubmit() {
      //e.preventDefault()
      const data = await axios.post('auth/login', { name: this.name, password: this.password }).then(data => {
        localstorageUtil.setItem('user', data.user);
        localstorageUtil.setItem('token', data.token);
        if (localstorageUtil.getItem('token') != null) {
          this.$emit('loggedIn');
          if (this.$route.params.nextUrl != null) {
            this.$router.push(this.$route.params.nextUrl);
          } else {
            let routePath = 'services';

            if(!this.setting.serviceMapping && this.setting.flowNetwork) {
              routePath = 'rfn'
            }

            this.$router.push({ path: routePath })
          }
        }
        this.msg = 'login succeeded';
      });
    },
    clear() {
      this.$refs.loginForm.reset();
    },
  },
};
</script>

